<template>
  <v-container fluid grid-list-md>
    <div v-infinite-scroll="loadMore" infinite-scroll-disabled="busy" infinite-scroll-distance="10">
      <v-card v-for="post in posts"  :key="post.id" class="elevation-3 ma-3">
        <v-card-title class="pa-0">
          <v-toolbar dense>
            <v-toolbar-title><router-link :to="{ name: 'Post', params: { postId: post.id }}">{{ post.title }}</router-link></v-toolbar-title>
            <v-spacer></v-spacer>
            <div>Опубликовано: {{ post.createdAt }}</div>
          </v-toolbar>
        </v-card-title>
        <v-card-text >
          <v-layout row wrap>
            <v-flex  md12 sm12 xs12 v-html="post.description">
            </v-flex>
          </v-layout>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn :to="{ name: 'Post', params: { postId: post.id }}">Читать дальше</v-btn>
        </v-card-actions>
      </v-card>
    </div>
    <v-layout column justify-center align-center v-if="busy && posts.length !== totalPosts" class="ma-3">
      <v-progress-circular indeterminate color="primary"></v-progress-circular>
    </v-layout>
  </v-container>
</template>

<script>
  import firebase from 'firebase'

  let postsRef

  export default {
    name: 'index',
    data: function () {
      return {
        posts: [],
        busy: false,
        totalPosts: 1
      }
    },
    // computed: {},
    created: function () {
      this.$store.commit('toolbar/title', 'Posts')
      postsRef = firebase.firestore().collection('posts').orderBy('createdAt', 'desc').limit(10)
      firebase.firestore().collection('posts').doc('meta').get().then((meta) => {
        this.totalPosts = meta.get('total')
      })
      // this.fetchPage()
      // firebase.database().ref('posts').orderByChild('createdAt').once('value', this.postsCallback)
    },
    methods: {
      fetchPage: function () {
        console.log('[start] fetchPage:' + new Date())
        postsRef.get().then((snapshot) => {
          let last = snapshot.docs[snapshot.docs.length - 1]
          postsRef = firebase.firestore().collection('posts').orderBy('createdAt', 'desc').startAfter(last).limit(2)
          let tmp = []
          snapshot.forEach((post) => {
            tmp.push({
              id: post.id,
              title: post.get('title'),
              description: post.get('description'),
              createdAt: (new Date(post.get('createdAt'))).toLocaleString(this.$store.state.lang, {
                year: 'numeric',
                month: 'numeric',
                day: 'numeric',
                timezone: 'UTC',
                hour: 'numeric',
                minute: 'numeric'
              })
            })
          })
          // tmp = tmp.reverse()
          // this.posts = tmp
          tmp.forEach((post) => {
            this.posts.push(post)
          })
          this.busy = false
          console.log('[end] fetchPage:' + new Date())
        })
      },
      loadMore: function () {
        console.log('load start!')
        if (!this.busy) {
          this.busy = true
          if (this.posts.length !== this.totalPosts) {
            this.fetchPage()
          }
        }
      }
    }
  }
</script>

<style>
</style>



