import menu from '@/store/menu'

export default {
  namespaced: true,
  state () {
    return {
      visible: true
    }
  },
  getters: {
  },
  mutations: {
    visible (state) {
      state.visible = !state.visible
    }
  },
  modules: {
    menu: menu
  }
}



