function findById (state, id) {
  return state.menu.find(item => item.id === id)
}

function findIndexById (state, id) {
  return state.menu.findIndex(item => item.id === id)
}

export default {
  namespaced: true,
  state () {
    return {
      menu: [{
        id: 'test',
        title: 'test',
        visible: true,
        callback: function () {
          console.log('test callback')
        }
      }]
    }
  },
  mutations: {
    add (state, menu) {
      state.menu.push(menu)
    },
    visible (state, id) {
      let item = findById(state, id)
      if (item !== undefined) {
        item.visible = !item.visible
      }
    },
    remove (state, id) {
      let index = findIndexById(state, id)
      if (index !== -1) {
        state.menu.splice(index, 1)
      }
    }
  }
}



