<template>
  <v-container fluid grid-list-md>
    <v-card :key="post.id" class="elevation-3 ma-3" v-if="loaded">
      <v-card-title class="pa-0">
        <v-toolbar dense>
          <v-toolbar-title><router-link :to="{ name: 'Post', params: { postId: post.id }}">{{ post.title }}</router-link></v-toolbar-title>
          <v-spacer></v-spacer>
          <div>Опубликовано: {{ post.createdAtLocale }}</div>
        </v-toolbar>
      </v-card-title>
      <v-card-text >
        <v-layout row wrap>
          <v-flex  md12 sm12 xs12 v-html="post.content">
          </v-flex>
        </v-layout>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-chip v-for="tag in post.tags" :key="tag"><router-link :to="{ name: 'Tag', params: { tagId: tag }}">{{ tag }}</router-link></v-chip>
      </v-card-actions>
    </v-card>
    <v-layout column justify-center align-center v-else class="ma-3">
      <v-progress-circular indeterminate color="primary"></v-progress-circular>
    </v-layout>
  </v-container>
</template>

<script>
  import firebase from 'firebase'

  export default {
    name: 'post',
    data: function () {
      return {
        post: {
          id: undefined,
          title: '',
          content: '',
          createdAtLocale: '',
          tags: []
        },
        loaded: false
      }
    },
    created: function () {
      firebase.firestore().collection('posts').doc(this.$route.params.postId).get().then((doc) => {
        if (doc.exists) {
          this.post.id = doc.id
          this.post.title = doc.get('title')
          this.post.content = doc.get('content')
          this.post.createdAtLocale = (new Date(doc.get('createdAt'))).toLocaleString(this.$store.state.lang, {
            year: 'numeric',
            month: 'numeric',
            day: 'numeric',
            timezone: 'UTC',
            hour: 'numeric',
            minute: 'numeric'
          })
          this.post.tags = doc.get('tags')
          this.loaded = true
        } else {
          this.$router.push({
            name: 'PageNotFound'
          })
        }
      })
    }
  }
</script>

<style scoped>

</style>



